.wrap {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;

  .lightbox-link:hover .img-portfolio {
    animation: in 4s ease-in forwards;
    transform-style: preserve-3d;
    will-change: transform;
    right: 0%;
  }
}

.lightbox-link {
  width: 100%;
  color: #fff;
  text-decoration: none;
  z-index: 20;

  &:hover {
    color: #0056b3;
    text-decoration: underline;
  }
}

.w-inline-block {
  max-width: 100%;
  display: inline-block;
}

.portfolio-wrapper {
  position: relative;
  overflow: hidden;
  height: 350px;
  padding-top: 20px;
  padding-left: 20px;
  border-radius: 4px;
  background-color: #5a61d6;
  cursor: pointer;
}

.overlay-portf {
  position: absolute;
  left: 0%;
  top: 0%;
  right: 0%;
  bottom: 0%;
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(
    180deg,
    rgba(25, 106, 145, 0.3),
    rgba(25, 106, 145, 0.3)
  );
  opacity: 0;
  transition: opacity 500ms ease;
  z-index: 5;

  &:hover {
    opacity: 1;
  }

  &:hover + .portfolio-title {
    color: #0056b3;
  }
}

.portfolio-title {
  z-index: 2;
  margin-top: 0px;
  margin-bottom: 0px;
  font-size: 23px;
  line-height: 105%;
  text-align: left;
  color: #000;
  text-transform: uppercase;

  &:hover {
    color: #0056b3;
  }
}

.btn.ghost {
  padding: 7px 26px;
  border-style: solid;
  border-width: 1px;
  border-color: #fff;
  background-color: transparent;
  font-size: 14px;
}

.img-portfolio {
  transform: scale(0.7) rotate(26deg);
  transform: translate3d(0%, 0%, 0px) scale3d(0.7, 0.7, 1) rotateX(0deg)
    rotateY(0deg) rotateZ(26deg) skew(0deg, 0deg);
  transform-style: preserve-3d;
  transition: 2s;
  position: absolute;
  left: auto;
  top: -100%;
  right: -10%;
  max-width: 100%;
  vertical-align: middle;
  display: inline-block;
  width: 100%;
  animation: out 1s ease-in forwards;
}

.portfolio-wrapper.sift {
  background-color: $sift;
}

.portfolio-wrapper.clockwise {
  background-color: $clockwise;
}

.portfolio-wrapper.spectre {
  background-color: $spectre;
}

.portfolio-wrapper.apres {
  background-color: $apres;
}

.portfolio-wrapper.unicorn {
  background-color: $unicorn;
}

@keyframes in {
  20% {
    transform: scale(0.7) rotateX(0deg) rotateZ(0deg) skew(0deg, 0deg);
  }
  35% {
    transform: scale(1);
  }
  65% {
    transform: translate3d(0%, 20%, 0px);
  }
  100% {
    transform: translate3d(0%, -41%, 0px);
  }
}

@keyframes out {
  from {
    transform: translate3d(0%, -41%, 0px) scale(1) rotateX(0deg) rotateZ(0deg)
      skew(0deg, 0deg);
  }
  to {
    transform: translate3d(0%, 0%, 0px) scale3d(0.7, 0.7, 1) rotateX(0deg)
      rotateY(0deg) rotateZ(26deg) skew(0deg, 0deg);
  }
}

@media (max-width: 991px) {
  .portfolio-wrapper {
    height: 650px;
  }

  .container.column .content._33 {
    width: 100%;
  }

  .section.dark {
    padding: 50px 10px;
  }
}

@media (max-width: 767px) {
  .portfolio-wrapper {
    height: 560px;
  }
}

@media (max-width: 479px) {
  .portfolio-wrapper {
    height: 420px;
  }

  .img-portfolio {
    right: -40%;
  }
}
